.style-over{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(195, 205, 215 ,0.1);
    
}

.style-container{
    position: fixed;
    width: 70rem;
    height: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50% , -50%);
    background-color: rgb(29, 30, 30);
    padding: 50px;
    border-radius: 20px;
}